import React, { useRef, useState, useEffect } from "react";
import { AccordionDataType } from "types";
import { Arrow } from "./Arrow";
import styles from "./styles.module.scss";

type ItemType = {
    data: AccordionDataType;
    isOpen: boolean;
    onClick: (index: number) => void;
    index: number;
};

export const Item: React.FC<ItemType> = React.memo(
    ({ data, isOpen, onClick, index }) => {
        const ref = useRef<HTMLDivElement>(null);
        const [height, setHeight] = useState(0);

        useEffect(() => {
            const content = ref.current;

            if (!content) {
                return;
            }

            if (isOpen) {
                setHeight(content.scrollHeight);
            } else {
                setHeight(0);
            }
        }, [isOpen]);

        const handleClick = React.useCallback(
            () => onClick(index),
            [onClick, index]
        );

        return (
            <li
                className={`${styles.accordionItem} ${
                    isOpen ? styles.active : ""
                } full-width`}
            >
                <h4 className={styles.accordionItemTitle}>
                    <button
                        className={styles.accordionItemBtn}
                        onClick={handleClick}
                    >
                        {data.title}
                        <Arrow />
                    </button>
                </h4>
                <div
                    className={styles.accordionItemContainer}
                    style={{ height }}
                >
                    <div ref={ref} className={styles.accordionItemContent}>
                        {data.content}
                    </div>
                </div>
            </li>
        );
    }
);
