import React, { useEffect, useState } from "react";
import * as Accordion from "components/accordion";
import { accordionData } from "pages/main/sections/faq/accordionData";
import * as Configuration from "modules/api/use-config";
import * as Icon from "./icons";
import { AccordionDataType } from "types";
import * as Search from "./search";
import { getNodeText } from "utils/get-node-text";

type ItemType = {
    icon: React.ReactNode;
    title: string;
    data: Array<AccordionDataType>;
};

const data = (config?: Configuration.ConfigurationValue): Array<ItemType> => [
    {
        icon: <Icon.Star />,
        title: "General questions",
        data: accordionData(config).filter(
            (item) => item.category === "general"
        ),
    },
    {
        icon: <Icon.Card />,
        title: "How to get?",
        data: accordionData(config).filter((item) => item.category === "get"),
    },
    {
        icon: <Icon.CircularArrow />,
        title: "How to return?",
        data: accordionData(config).filter(
            (item) => item.category === "return"
        ),
    },
    {
        icon: <Icon.Question />,
        title: "Other",
        data: accordionData(config).filter((item) => item.category === "other"),
    },
];

const Item: React.FC<{ item: ItemType }> = ({ item }) => {
    return (
        <div className="rounded-xl bg-white shadow-popUp mt-4 mobile:mt-2 p-4 pb-10 mobile:pb-6">
            <div className="flex items-center mobile:[&>svg]:w-6 mobile:[&>svg]:h-6">
                {item.icon}
                <h3 className="ml-2 text-2xl mobile:text-base font-semibold">
                    {item.title}
                </h3>
            </div>
            <Accordion.Layout items={item.data} />
        </div>
    );
};

export const Layout = () => {
    const config = Configuration.useConfig();
    const [searchValue, setSearchValue] = useState<string>("");
    const [foundData, setFoundData] = useState<Array<AccordionDataType>>([]);
    const isSearchInProgress = searchValue.length > 0;

    useEffect(() => {
        if (isSearchInProgress) {
            setFoundData(
                accordionData(config).filter(
                    (item) =>
                        item.title
                            .toLowerCase()
                            .includes(searchValue.toLowerCase()) ||
                        getNodeText(item.content)
                            .toLowerCase()
                            .includes(searchValue.toLowerCase())
                )
            );
        }
    }, [isSearchInProgress, searchValue, setFoundData, config]);

    const handleSearchChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        setSearchValue(e.target.value);
    };

    const handleDelete = React.useCallback(() => {
        isSearchInProgress && setSearchValue("");
    }, [isSearchInProgress, setSearchValue]);

    return (
        <section className="section section-content">
            <div className="wide-container flex-col pb-[120px] mobile:pb-20 pt-3 mobile:pt-8">
                <Search.Layout
                    value={searchValue}
                    onChange={handleSearchChange}
                >
                    <Search.Button value={searchValue} onClick={handleDelete} />
                </Search.Layout>
                {isSearchInProgress ? (
                    foundData.length !== 0 ? (
                        <Accordion.Layout items={foundData} />
                    ) : (
                        <p className="text-xl font-semibold mt-5 mobile:mt-2 mobile:text-sm">
                            Unfortunately, nothing was found. Please clarify
                            your request.
                        </p>
                    )
                ) : (
                    data(config).map((item) => (
                        <Item key={item.title} item={item} />
                    ))
                )}
            </div>
        </section>
    );
};
