import React from "react";
import classNames from "classnames";
import styles from "./styles.module.scss";
import { useOpenState } from "@horat1us/react-hooks";

export const Layout: React.FC<{
    value: string;
    onChange: (event: React.ChangeEvent<HTMLInputElement>) => void;
    children: React.ReactNode;
}> = ({ value, onChange, children }) => {
    const [isShown, handleIsShown] = useOpenState();

    return (
        <div className={styles.search}>
            <div
                className={classNames(
                    styles.searchGroup,
                    isShown ? styles.isFocused : ""
                )}
            >
                <input
                    className={styles.searchInput}
                    placeholder="Enter a search term"
                    onChange={onChange}
                    value={value}
                    onFocus={handleIsShown}
                    onBlur={handleIsShown}
                />
                {children}
            </div>
            {!isShown && (
                <p>Type your question or topic into the search bar.</p>
            )}
        </div>
    );
};
